import React, { useState, useEffect } from "react";
import "./EditCostForm.css";

function EditIncomeForm(props) {
  const [enteredType, setEnteredType] = useState("");
  const [enteredAmount, setEnteredAmount] = useState("");
  const [enteredDate, setEnteredDate] = useState("");

  useEffect(() => {
    if (props.income) {
      setEnteredType(props.income.type || "");
      setEnteredAmount(props.income.amount || "");
      setEnteredDate(
        props.income.date
          ? new Date(props.income.date).toISOString().split("T")[0]
          : ""
      );
    }
  }, [props.income]);

  const typeChangeHandler = (event) => setEnteredType(event.target.value);
  const amountChangeHandler = (event) => setEnteredAmount(event.target.value);
  const dateChangeHandler = (event) => setEnteredDate(event.target.value);

  const submitHandler = (event) => {
    event.preventDefault();

    const updatedIncome = {
      id: props.income.id,
      type: enteredType,
      amount: parseFloat(enteredAmount),
      date: new Date(enteredDate),
      
    };

    props.onSave(updatedIncome);
  };

  return (
    <form className="edit-cost-form" onSubmit={submitHandler}>
      <div className="edit-cost-form__controls">
        <div className="edit-cost-form__control">
          <label>Type</label>
          <input type="text" value={enteredType} onChange={typeChangeHandler} />
        </div>
        <div className="edit-cost-form__control">
          <label>Amount</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={enteredAmount}
            onChange={amountChangeHandler}
          />
        </div>
        <div className="edit-cost-form__control">
          <label>Date</label>
          <input
            type="date"
            value={enteredDate}
            onChange={dateChangeHandler}
          />
        </div>
      </div>
      <div className="edit-cost-form__actions">
        <button type="submit">Save</button>
        <button type="button" onClick={props.onCancel}>
          Cancel
        </button>
      </div>
    </form>
  );
}

export default EditIncomeForm;
