import Bar from './Bar';
import './Diagram.css';

const Diagram = (props) => {
  const maxMonthCost = Math.max(1, ...props.dataSet.map(data => data.value)); // Минимум 1, чтобы избежать деления на 0

  return (
    <div className="diagram">
      {props.dataSet.map((data) => (
        <div key={data.label} className="diagram-bar-container">
          <Bar value={data.value} maxValue={maxMonthCost} label={data.label} />
          <div className="bar-cost">${data.value}</div>
        </div>
      ))}
    </div>
  );
};

export default Diagram;
