import React, { useState, useEffect } from "react";
import NewCost from "./components/NewCost/NewCost";
import NewIncome from "./components/NewCost/NewIncome";
import ItemList from "./components/ItemList";
import { supabase } from "./supabaseClient";
import Auth from "./components/Auth";
import Header from "./components/Header";
import IncomeList from "./components/IncomeList";
import EditIncomeForm from "./components/EditIncomeForm";

function App() {
  const [costs, setCosts] = useState([]);
  const [incomes, setIncomes] = useState([]); // Для доходов
  const [user, setUser] = useState(null);

  // Загрузка текущего пользователя и данных
  useEffect(() => {
    const initializeUser = async () => {
      try {
        const { data: sessionData, error } = await supabase.auth.getSession();
        if (error) {
          console.error("Error fetching session:", error);
          return;
        }

        const currentUser = sessionData?.session?.user || null;
        setUser(currentUser);

        if (currentUser) {
          fetchCosts(currentUser.id);
          fetchIncomes(currentUser.id); // Загрузка доходов
        }

        const { data: subscription } = supabase.auth.onAuthStateChange(
          (event, session) => {
            const updatedUser = session?.user || null;
            setUser(updatedUser);

            if (updatedUser) {
              fetchCosts(updatedUser.id);
              fetchIncomes(updatedUser.id);
            } else {
              setCosts([]);
              setIncomes([]);
            }
          }
        );

        return () => {
          subscription?.unsubscribe();
        };
      } catch (error) {
        console.error("Error initializing user:", error);
      }
    };

    initializeUser();
  }, []);

  // Получение расходов из базы
  const fetchCosts = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("costs")
        .select("*")
        .eq("user_id", userId);

      if (error) {
        console.error("Error fetching costs:", error);
        return;
      }

      const transformedData = data.map((item) => ({
        ...item,
        date: new Date(item.date),
      }));
      setCosts(transformedData);
    } catch (error) {
      console.error("Error in fetchCosts:", error);
    }
  };

  // Получение доходов из базы
  const fetchIncomes = async (userId) => {
    try {
      const { data, error } = await supabase
        .from("debet")
        .select("*")
        .eq("user_id", userId);

      if (error) {
        console.error("Error fetching incomes:", error);
        return;
      }

      const transformedData = data.map((item) => ({
        ...item,
        date: new Date(item.date),
      }));
      setIncomes(transformedData);
    } catch (error) {
      console.error("Error in fetchIncomes:", error);
    }
  };

  // Добавление нового расхода
  const addCostHandler = async (cost) => {
    try {
      const { error } = await supabase.from("costs").insert({
        user_id: user.id,
        description: cost.description,
        price: cost.price,
        date: cost.date.toISOString(),
        category: cost.category,
      });

      if (error) {
        console.error("Error adding cost:", error);
        return;
      }

      fetchCosts(user.id);
    } catch (error) {
      console.error("Error in addCostHandler:", error);
    }
  };

  // Обновление расхода
  const updateCostHandler = async (updatedCost) => {
    try {
      const { error } = await supabase
        .from("costs")
        .update({
          description: updatedCost.description,
          price: updatedCost.price,
          date: updatedCost.date.toISOString(),
          category: updatedCost.category,
        })
        .eq("id", updatedCost.id);

      if (error) {
        console.error("Error updating cost:", error);
        return;
      }

      // Обновить локальное состояние, чтобы отобразить изменения без повторной загрузки
      setCosts((prevCosts) =>
        prevCosts.map((cost) =>
          cost.id === updatedCost.id ? { ...cost, ...updatedCost } : cost
        )
      );
    } catch (error) {
      console.error("Error in updateCostHandler:", error);
    }
  };

  // Удаление расхода
  const deleteCostHandler = async (costId) => {
    try {
      const { error } = await supabase.from("costs").delete().eq("id", costId);

      if (error) {
        console.error("Error deleting cost:", error);
        return;
      }

      fetchCosts(user.id);
    } catch (error) {
      console.error("Error in deleteCostHandler:", error);
    }
  };

  // Добавление дохода
  const addIncomeHandler = async (income) => {
    try {
      const date = income.date ? new Date(income.date).toISOString() : null;

      const { error } = await supabase.from("debet").insert({
        user_id: user.id,
        type: income.type,
        amount: income.amount,
        date: date,
      });

      if (error) {
        console.error("Error adding income:", error);
        return;
      }

      fetchIncomes(user.id);
    } catch (error) {
      console.error("Error in addIncomeHandler:", error);
    }
  };

  // Выход из аккаунта
  const handleLogout = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) {
        console.warn("Sign-out error occurred, but clearing local state.");
      }
      setUser(null);
      setCosts([]);
      setIncomes([]);
    } catch (error) {
      console.error("Error in handleLogout:", error);
    }
  };

  // Рассчитываем сбережения за месяц
  const calculateSavings = () => {
    const currentMonth = new Date().getMonth();
    const filteredIncomes = incomes.filter(
      (income) => new Date(income.date).getMonth() === currentMonth
    );
    const filteredCosts = costs.filter(
      (cost) => new Date(cost.date).getMonth() === currentMonth
    );

    const totalIncome = filteredIncomes.reduce(
      (sum, income) => sum + income.amount,
      0
    );
    const totalCost = filteredCosts.reduce((sum, cost) => sum + cost.price, 0);

    return totalIncome - totalCost;
  };

  const updateIncomeHandler = async (updatedIncome) => {
  try {
    const { error } = await supabase
      .from("debet")
      .update({
        type: updatedIncome.type,
        amount: updatedIncome.amount,
        date: updatedIncome.date.toISOString(),
      })
      .eq("id", updatedIncome.id);

    if (error) {
      console.error("Error updating income:", error);
      return;
    }

    setIncomes((prevIncomes) =>
      prevIncomes.map((income) =>
        income.id === updatedIncome.id ? { ...income, ...updatedIncome } : income
      )
    );
  } catch (error) {
    console.error("Error in updateIncomeHandler:", error);
  }
};

// Удаление дохода
const deleteIncomeHandler = async (incomeId) => {
  try {
    const { error } = await supabase.from("debet").delete().eq("id", incomeId);

    if (error) {
      console.error("Error deleting income:", error);
      return;
    }

    setIncomes((prevIncomes) =>
      prevIncomes.filter((income) => income.id !== incomeId)
    );
  } catch (error) {
    console.error("Error in deleteIncomeHandler:", error);
  }
};

const [editingIncome, setEditingIncome] = useState(null);

const editIncomeHandler = (incomeId) => {
  const incomeToEdit = incomes.find((income) => income.id === incomeId);
  setEditingIncome(incomeToEdit);
};

const saveEditedIncomeHandler = (updatedIncome) => {
  updateIncomeHandler(updatedIncome);
  setEditingIncome(null);
};
  return (
    <div>
    {user && <Header user={user} onLogout={handleLogout} />}
    {!user && <Auth onAuthenticated={(user) => setUser(user)} />}
    {user && (
      <>
        <div className="savings-container">
          <h2>Your Savings for this Month: ${calculateSavings()}</h2>
        </div>
        <NewCost onAddCost={addCostHandler} />
        <NewIncome onAddIncome={addIncomeHandler} />

        {/* Рендеринг формы редактирования доходов */}
        {editingIncome && (
          <EditIncomeForm
            income={editingIncome}
            onSave={saveEditedIncomeHandler}
            onCancel={() => setEditingIncome(null)}
          />
        )}

        {/* Отображение расходов и доходов */}
        <div className="items-container">
          <ItemList
            costs={costs}
            onUpdateCost={updateCostHandler}
            onDeleteCost={deleteCostHandler}
          />
          <IncomeList
            incomes={incomes}
            onEditIncome={editIncomeHandler}
            onDeleteIncome={deleteIncomeHandler}
          />
        </div>
      </>
    )}
  </div>
  );
}

export default App;
