import React from "react";
import IncomeItem from "./IncomeItem";
import "./CostList.css";

const IncomeList = (props) => {
  if (props.incomes.length === 0) {
    return <h2 className="cost-list__fallback">No incomes available</h2>;
  }

  return (
    <ul className="cost-list">
      {props.incomes.map((income) => (
        <IncomeItem
  key={income.id}
  id={income.id}
  date={income.date}
  type={income.type} /* Передаем тип */
  amount={income.amount}
  onDelete={props.onDeleteIncome}
  onEdit={props.onEditIncome}
/>
      ))}
    </ul>
  );
};

export default IncomeList;
