import { useState } from "react";
import IncomeForm from "./IncomeForm"; // Новый компонент для формы доходов
import "./NewIncome.css";

const NewIncome = (props) => {
  const [isFormVisible, setVisible] = useState(false);

  const saveIncomeDataHandler = (inputIncomeData) => {
    const incomeData = {
      ...inputIncomeData,
      id: Math.random().toString(),
    };

    props.onAddIncome(incomeData);
    setVisible(false);
  };

  const inputIncomeDataHandler = () => {
    setVisible(true);
  };

  const cancelIncomeHandler = () => {
    setVisible(false);
  };

  return (
    <div className="new-income">
      {!isFormVisible && (
        <button onClick={inputIncomeDataHandler}>Add new income</button>
      )}
      {isFormVisible && (
        <IncomeForm
          onSaveIncomeData={saveIncomeDataHandler}
          onCancel={cancelIncomeHandler}
        />
      )}
    </div>
  );
};

export default NewIncome;
